			<span class="by">A project of <a class="link" href="https://metalabharvard.github.io/">metalab (at) Harvard</a><br> and part of <a class="link" href="https://curricle.berkman.harvard.edu/">Curricle research</a></span>

			<span class="stories-intro"><b>Data Narratives:</b> <br>Select a story on teaching and learning over time</span>

			<div class="stories-wrapper">
				<div @click="dispatchCurrentStory(index)" class="story-outer" v-for="(story,index) in storiesArr" :key="story.id">
					<!-- <span class="number">{{ `0${index + 1}` }}</span> -->
					<span>{{ story.introduction.title }}</span>
					<span class="subtitle">{{ introContent[index] }}</span>
				</div>
			</div>

			<div class="intro--text-wrapper">
				<p class="intro--text">A research project of <a class="link" href="https://metalabharvard.github.io/">metaLAB (at) Harvard</a>, <b>Curricle Lens</b> uses data narration to explore the conversational nature of the college curriculum. With comprehensive data on more than three decades of course offerings, we're able to detect historical trends. Shifts in course titles and formats allow us to trace the signals of topics, issues, and meanings. And by collating these data shifts with historical events, we can gauge the pulse of external events—social movements; musical genres; literary publications—that shape and redirect the ordered progress of college teaching and learning. Our data include the contents of online Harvard course catalogues from 1990 to the present, archived by the office of the Registrar, with data from earlier decades we've developed from archival sources. <b>Curricle Lens</b> offers a series of data stories that visualize discrete patterns in these data over time, enfolded with and enlivened by essays that ground and contextualize the data with archival material, storytelling, and argument. These stories are provocations to further exploration. The curriculum takes surprising turns throughout its journey—and we haven't found all the twists yet. <b>Curricle Lens</b> is an invitation: to join us in exploring these and other stories of teaching and learning, at Harvard and beyond.</p>

				<p class="intro--text"><i>Designed, developed, and narrated by <a class="link" href="https://kimalbrecht.com/">Kim Albrecht</a>, <a class="link" href="https://metalabharvard.github.io/people/matthew">Matthew Battles</a>, <a class="link" href="https://www.fabiandinklage.com/">Fabian Dinklage</a>, and <a class="link" href="https://metalabharvard.github.io/about/">Sydney Lewis</a></i></p>

