<script>
	import Share from './Share.vue';
	import LineStackingIntro from './LineStackingIntro.vue';
	import { select as d3Select } from 'd3';


	export default {
		name: 'Intro',
		props: ['content'],
		data() {
			return {
				introContent: {
					0: 'Investigates patterns of course offerings in the sciences and the humanities, arguing for discernible differences in approaches to teaching and learning between what C. P. Snow called the "two cultures" of academic life.',
					1: 'Examines the establishment of a popular musical genre as a topic of scholarly study, as it makes its way from controversy into the curriculum.',
					2: 'Seeks out the surprising impact of the material turn in literary studies across disciplines, from the arts and humanities to the social sciences and caring professions.',
					3: 'Tracing two famous authors though their own course choices, and the impact they have on teaching today.',
					4: 'Looks at the rise of this preeminent Harvard program, and the invisible curriculum sought out—and protested—by black students at Harvard before its founding.'
				},
				statements: {
					year: 'year',
					lineGroup: 'externalCourseId',
					keyword: 'COMPUTER_SCIENCE',
					keywordTitle: 'Computer Science',
					startYear: 1990,
					endYear: 2010,
					searchMode: "department",
					areaGroup: 'component', // subjectDescription // component // externalCourseId
					maxCall: 10000
				}
			}
		},
		components: {
			Share,
			LineStackingIntro
		},
		computed: {
            clientWidth: () => {
                return d3Select('#app').node().getBoundingClientRect().width;
            },
            clientHeight: () => {
                return d3Select('#app').node().getBoundingClientRect().height;
			},
			storiesArr() {
				return this.content.stories;
			}
		},
		methods: {
			dispatchIntroVisible(state) {
				this.$store.dispatch('SET_INTRO_VISIBLE', state);
			},
			toggleIntroVisible() {
				d3Select('.intro').classed('hide', true);
				setTimeout(() => {
					this.dispatchIntroVisible(false);
				}, 500)
			},
            dispatchCurrentStory(state) {
				d3Select('.intro').classed('hide', true);
				d3Select('.Nav').style('display', 'flex');
				setTimeout(() => {
					this.dispatchIntroVisible(false);
				}, 500)
                this.$store.dispatch("SET_CURRENT_STORY", state);
            },
		},
		created() {
			d3Select('.intro').style('width', this.clientWidth);
			d3Select('.intro').style('height', this.clientHeight);
		},
		mounted() {
			let width = window.innerWidth;
			const wrapperIntro = document.querySelector('.wrapper-intro');
			const nav = document.querySelector('.Nav');
			

			if (Number(width) < 321) {
				let newMargin = (width - 320) / 2;
				wrapperIntro.setAttribute('style', `margin-left: ${newMargin - 1}px`);
			} else if (Number(width) < 381) {
				let newMargin = (width - 320) / 2;
				wrapperIntro.setAttribute('style', `margin-left: ${newMargin}px`);
			} else if (Number(width) < 768) {
				let newMargin = (width - 320) / 2;
				wrapperIntro.setAttribute('style', `margin-left: ${newMargin}px`);
			} else {
				// wrapperIntro.setAttribute('style', `margin-left: ${0}px`);
				let newMargin = (width - 520) / 2;
                wrapperIntro.setAttribute('style', `margin-left: ${newMargin}px`);
			}
			d3Select('.subtitle').classed('visible', true);
			d3Select('.intro--text').classed('visible', true);
			d3Select('.btn').classed('visible', true);
			d3Select('.by').classed('visible', true);
		}
	}
</script>

